import { pick } from 'lodash';
import FieldNormalizer from 'field-normalizer';

// Project modules
import Api from 'utils/api';

const Members = {
  getMembers(params) {
    const body = FieldNormalizer.toSnakeCase(pick(params, [
      'onlyActive', 'locationId'
    ]));
    return Api.get('/members', body);
  },
  getMember(id) {
    return Api.get(`/members/${id}`);
  },
  blockMember(id) {
    return Api.put(`/members/${id}/block`);
  },
  updateMember({ id, name, lastname, phone, role, job_role, locationIds }) {
    return Api.put(
      `/members/${id}`,
      { id, name, lastname, phone, role, job_role, location_ids: locationIds }
    );
  },
  emailCheck(email) {
    return Api.get('/members/email_check', { email });
  },
  createMember({ name, lastname, phone, role, email, jobRole, locationIds }) {
    return Api.post(
      '/members?send_invite=true',
      { name, lastname, phone, role, email, job_role: jobRole, location_ids: locationIds }
    );
  },
  resendInvitationEmail(id) {
    return Api.get('/members/resend_invitation_email', { id });
  },
  unblockMember(id) {
    return Api.put(`/members/${id}/unblock`);
  }
};

export default Members;
